@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
*{font-family: 'Libre Baskerville', serif; outline-width: 0px; border: none;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#homepage{padding:20px;min-height:100vh;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover}#homepage .header{text-align:center;color:white}#homepage .header h1{margin-bottom:0px}#homepage .course{display:flex;flex-direction:column;flex-wrap:wrap;margin:50px auto}#homepage .course>div{box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);opacity:0.8;width:50vw;margin:10px auto;padding:20px;background:white;border-radius:5px}#homepage .course .course-content h1{margin:0px;padding:0px;font-size:25px}#homepage .course .course-content .instructor{margin:0px;padding:0px;font-size:15px}#homepage .course .course-content .subtitle{margin-bottom:30px}#homepage .course .course-content a{padding:10px;text-decoration:none;color:white;border-radius:5px}#homepage .course .course-content .browse-course{background:#17a2b8;margin-right:10px}#homepage .course .course-content .browse-problems{background:#28a745}#homepage .fotter{text-align:center;color:white;font-size:20px;margin-bottom:10px}#homepage .fotter a{color:white !important;text-decoration:none}#homepage .fotter span{color:red}

.fotter{text-align:center;color:white;font-size:20px;margin-bottom:10px}.fotter a{color:white !important;text-decoration:none}.fotter span{color:red}

#course-page{padding:20px;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover}#course-page .header{text-align:center;color:white}#course-page .header h1{margin-bottom:0px;text-transform:uppercase}

